import React, { useState } from 'react'
import { motion } from 'framer-motion'

export default function Trick() {
    const [state, setstate] = useState("^_^")
    function handleClick(){
        setstate("üòù")
    }
    return (
        <div className="trick">
            <motion.h3 onClick={handleClick} onHoverStart={handleClick} drag dragConstraints={{left: -10, top: -5, right: 10, bottom: 5}} whileHover={{cursor: 'text'}} >H487DN48F4</motion.h3>
            <div>Copy this code and send it in telegram and it will turn into an emoji</div>
            <div className="emoji">{state}</div>
        </div>
    )
} 
