import React, { useState } from 'react'
import { motion } from 'framer-motion'

function Trick2() {
    const [state, setstate] = useState(" ")
    function handleClick(){
        setstate("Doneüòù")
    }
    return (
        <div className="trick">
            <motion.button onClick={handleClick} whileHover={{ y:-100 }} transition={{mass: 1.5, type: "spring", duration: 0}} >Click me!</motion.button>
            Click here to get rewarded 
            <div>{state}</div>
        </div>
    )
}

export default Trick2
